import { Directive, Input } from '@angular/core';
import { BaseCdsDirective } from '../../cds-base';

@Directive({ selector: 'cds-button' })
export class CdsButtonDirective extends BaseCdsDirective {
  get action() {
    return this.element['action'];
  }
  @Input() set action(value: any) {
    this.element['action'] = value;
  }
  get status() {
    return this.element['status'];
  }
  @Input() set status(value: any) {
    this.element['status'] = value;
  }
  get size() {
    return this.element['size'];
  }
  @Input() set size(value: any) {
    this.element['size'] = value;
  }
  get block() {
    return this.element['block'];
  }
  @Input() set block(value: any) {
    if (value === '') {
      this.element['block'] = true;
      return;
    }
    this.element['block'] = value;
  }
  get loadingState() {
    return this.element['loadingState'];
  }
  @Input() set loadingState(value: any) {
    this.element['loadingState'] = value;
  }
  get readonly() {
    return this.element['readonly'];
  }
  @Input() set readonly(value: any) {
    if (value === '') {
      this.element['readonly'] = true;
      return;
    }
    this.element['readonly'] = value;
  }
  get type() {
    return this.element['type'];
  }
  @Input() set type(value: any) {
    this.element['type'] = value;
  }
  get name() {
    return this.element['name'];
  }
  @Input() set name(value: any) {
    this.element['name'] = value;
  }
  get value() {
    return this.element['value'];
  }
  @Input() set value(value: any) {
    this.element['value'] = value;
  }
  get disabled() {
    return this.element['disabled'];
  }
  @Input() set disabled(value: any) {
    if (value === '') {
      this.element['disabled'] = true;
      return;
    }
    this.element['disabled'] = value;
  }
  get ariaDisabled() {
    return this.element['ariaDisabled'];
  }
  @Input() set ariaDisabled(value: any) {
    this.element['ariaDisabled'] = value;
  }
}
