import { Directive, Input } from '@angular/core';
import { BaseCdsDirective } from '../../cds-base';

@Directive({ selector: 'cds-card' })
export class CdsCardDirective extends BaseCdsDirective {
  get role() {
    return this.element['role'];
  }
  @Input() set role(value: any) {
    this.element['role'] = value;
  }
  get globalStyles() {
    return this.element['globalStyles'];
  }
  @Input() set globalStyles(value: any) {
    this.element['globalStyles'] = value;
  }
}
