import { Directive, Input } from '@angular/core';
import { BaseCdsDirective } from '../../cds-base';

@Directive({ selector: 'cds-navigation-item' })
export class CdsNavigationItemDirective extends BaseCdsDirective {
  get i18n() {
    return this.element['i18n'];
  }
  @Input() set i18n(value: any) {
    this.element['i18n'] = value;
  }
  get active() {
    return this.element['active'];
  }
  @Input() set active(value: any) {
    if (value === '') {
      this.element['active'] = true;
      return;
    }
    this.element['active'] = value;
  }
  get disabled() {
    return this.element['disabled'];
  }
  @Input() set disabled(value: any) {
    if (value === '') {
      this.element['disabled'] = true;
      return;
    }
    this.element['disabled'] = value;
  }
  get role() {
    return this.element['role'];
  }
  @Input() set role(value: any) {
    this.element['role'] = value;
  }
  get expanded() {
    return this.element['expanded'];
  }
  @Input() set expanded(value: any) {
    if (value === '') {
      this.element['expanded'] = true;
      return;
    }
    this.element['expanded'] = value;
  }
  get groupItem() {
    return this.element['groupItem'];
  }
  @Input() set groupItem(value: any) {
    if (value === '') {
      this.element['groupItem'] = true;
      return;
    }
    this.element['groupItem'] = value;
  }
  get hasFocus() {
    return this.element['hasFocus'];
  }
  @Input() set hasFocus(value: any) {
    this.element['hasFocus'] = value;
  }
  get focusElement() {
    return this.element['focusElement'];
  }
  @Input() set focusElement(value: any) {
    this.element['focusElement'] = value;
  }
  get itemText() {
    return this.element['itemText'];
  }
  @Input() set itemText(value: any) {
    this.element['itemText'] = value;
  }
}
