import { Directive, Input } from '@angular/core';
import { BaseCdsDirective } from '../../cds-base';

@Directive({ selector: 'cds-modal-content' })
export class CdsModalContentDirective extends BaseCdsDirective {
  get shadowRootOptions() {
    return this.element['shadowRootOptions'];
  }
  @Input() set shadowRootOptions(value: any) {
    this.element['shadowRootOptions'] = value;
  }
}
